@font-face {
    font-family: 'Neurial Grotesk';
    src: url('hinted-NeurialGrotesk-Bold.eot');
    font-weight: bold;
    font-style: normal;
}
@font-face {
    font-family: 'Neurial Grotesk';
    src: local('Neurial Grotesk Bold'), local('NeurialGrotesk-Bold'),
        url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
        url(data:application/font-woff;charset=utf-8;base64,) format('woff'),
        url('hinted-NeurialGrotesk-Bold.ttf') format('truetype'),
        url('hinted-NeurialGrotesk-Bold.svg#NeurialGrotesk-Bold') format('svg');
    font-weight: bold;
    font-style: normal;
}

